.myOrdersPage {
  width: 100vw;
  max-width: 100%;
  padding: 0 7vmax;
  padding-top: 4vmax;
  padding-bottom: 2vmax;

  box-sizing: border-box;
  background-color: rgb(235, 235, 235);
  /* position: fixed; */
  top: 0;
  left: 0;
  height: auto;
  margin-bottom: 4rem;
  display: flex;
  flex-direction: column;
}

#myOrdersHeading {
  text-align: center;
  font: 400 1.2vmax "Roboto";
  padding: 0.5vmax;
  box-sizing: border-box;
  color: rgb(255, 255, 255);
  transition: all 0.5s;
  background-color: rgb(44, 44, 44);
}

.myOrdersTable {
  background-color: white;
}

.myOrdersTable div {
  font: 300 1vmax "Roboto";
  color: rgba(0, 0, 0, 0.678);
  border: none;
}

.myOrdersTable a {
  color: rgba(0, 0, 0, 0.527);
  transition: all 0.5s;
}

.myOrdersTable a:hover {
  color: tomato;
}

.MuiDataGrid-columnHeader {
  background-color:rgb(250, 183, 0);
  padding: 1vmax !important;
}

.MuiDataGrid-columnHeader div {
  color: rgb(0, 0, 0);
  font: 500 1.1vmax "Roboto" !important;
}

.MuiDataGrid-iconSeparator {
  display: none !important;
}

@media screen and (max-width: 600px) {
  .myOrdersPage {
    height: 93vh;
    padding: 0;
     /* padding: 0 6vmax; */
    padding-top: 15vmax;
  /* padding-bottom: 2vmax; */
  }

  #myOrdersHeading {
    font: 400 2.2vmax "Roboto";
    padding: 4vw;
  }

  .myOrdersTable div {
    font: 300 4vw "Roboto";
  }

  .MuiDataGrid-columnHeader {
    padding: 20px !important;
  }

  .MuiDataGrid-columnHeader div {
    font: 500 5vw "Roboto" !important;
  }
}