.bannerH {
   display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.5);
  color: var(--mainWhite);
  padding: 5rem 5rem;
  text-align: center;
  text-transform: capitalize;
  letter-spacing: var(--mainSpacing);
   width: 40vw;
    height: 40vh;

  /* margin-left: 20vmax; */
  /* margin-right: 20vmax; */
  /* margin-top: 10vmax; */
}

.bannerH h1 {
  font-size: 2.5vw;
    font-family: "Belleza", sans-serif;

}
.bannerH div {
  width: 15rem;
  height: 5px;
  background:rgba(253, 253, 253, 0.5);
  margin: 1.7rem auto;
}
.bannerH p {
  font-size: 1.2vw;
  /* margin-bottom: 2rem; */
    font-family:Arial, Helvetica, sans-serif;


}
.Scroll {
  width: 7vw;
  height: 3vw;
    cursor: pointer;
    background-color: #f3b925;
    font-family: "Belleza", sans-serif;
    font-size: 0.8vw;
    font-weight: 600;
    color: #000;
    text-transform: uppercase;
    border: none;
    border-radius: 1rem;

  
}


.Scroll:hover{
    
     /* background: #4568dc; /* fallback for old browsers */
  /* background: -webkit-linear-gradient(
    to right,
    #b06ab3,
    #4568dc
  ); /* Chrome 10-25, Safari 5.1-6 */
  /* background: linear-gradient(
    to right,
    #b06ab3,
    #4568dc
  ); W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ 
  box-shadow: 0px 0px 3vmax #000;
  /* background: transparent; */
  scale: 1.1;
  transition: 0.5s ease;
  background: transparent;
  color: aliceblue;
     
    
    
}

@media screen and (max-width: 1068px) {
  .bannerH {
    width: 40vw;
    height: 20vh;
     
  }

}


@media screen and (max-width: 768px) {

  .bannerH {

    width: 10rem;
    height: 14rem;
   display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background:transparent;
  color: black;
 
  text-align: center;
  text-transform: capitalize;
  letter-spacing: var(--mainSpacing);
}

.bannerH h1 {
  font-size: 2.1rem;
  width: 70vw;
  font-family: "ABEEZEE", sans-serif;
}
.bannerH p {
  font-size: 1.2rem;
   width: 50vw;
  margin-bottom: 2rem;
}
.Scroll {
    width: 20vw;
    height:8vw;
    font-family: "Belleza", sans-serif;
    font-size: 1.9vw
}

  
}
@media screen and (max-width: 508px) {

    .bannerH {

    width: 9rem;
    height: 9rem;
  
}

.bannerH h1 {
    font-size: 1.8rem;
    width: 70vw;
    font-family: "Belleza", sans-serif;

}
.bannerH p {
   font-size: 1.1rem;
   width: 50vw;
  margin-bottom: 2rem;
  font-family: "ABEEZEE", sans-serif;
}
.Scroll {
  width: 5rem;
  height: 2rem;
    font-family: "Belleza", sans-serif;
    font-size: 0.5rem;
}
  
}
@media screen and (max-width: 378px) {
.bannerH{
  width: 2rem;
  height: 12rem;
  font-family: Belleza,sans-serif;
}
.bannerH h1{
  font-size: 1.2rem;
}
.bannerH p {
   font-size: 0.7rem;
   width: 50vw;
  margin-bottom: 2rem;
  font-family: "ABEEZEE", sans-serif;
}
  

}